import { Injectable } from '@angular/core';

import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';



@Injectable()
export class RestService {


  constructor(public http: Http) {

  }

  user(id, cb)
  {
  		this.http.get('http://localhost:3333/user?id=' + id).map(res => res.json()).subscribe(data => {
  			cb(data)
  		});
  }
  connect(credentials, cb)
  {
  	this.http.post('http://localhost:3333/auth', credentials).map(res => res.json()).subscribe(data => {
  		cb(data)
  	})
  }

}



