import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { RestService } from '../../services/rest.service';

import * as $ from 'jquery'

import * as moment from 'moment'


import io from 'adonis-websocket-client';


@Component({
  selector: 'app-trip',
  templateUrl: './trip.component.html',
  styleUrls: ['./trip.component.css']
})
export class TripComponent implements OnInit {

	
	user: any;
	socket: any;
    channel: any;
	filter: any = 'all';
	showNew: any = false;

	newPayment: any = {
		name: 'Empresa',
		description: 'Descripción',
		date: 'Fecha',
		payment: 'Importe'
	}

  	constructor(public router: Router, public rest: RestService) { 

  		moment.locale('es')

  		if(localStorage.getItem('auth') == null)
  		{
  			this.router.navigate(['/'])
  		}
  		else{

  			this.user = JSON.parse(localStorage.getItem('auth'))


  			this.rest.user(this.user.id, (data) => {
      			this.user = data;

      			for(let trip of data.trips)
      			{
      				if(trip.status == 'activo')
      				{
      					let budget = 0;

      					for(let pay of trip.payments)
      					{
      						budget = Number(pay.payment) + budget;
      					}

                        trip.payed = budget;
                        trip.percentage = (100-(Number(trip.payed)*100)/Number(trip.budget)).toFixed(0);
      				    trip.payed = Number(trip.payed).toFixed(2);
                        trip.budget = Number(trip.budget).toFixed(2)
                        trip.budget_cur = (trip.budget - trip.payed).toFixed(2);
                        $('.budget-bar').css('width', trip.percentage + '%')


    				    this.user.trip = trip;


    				    let start = moment(this.user.trip.date_start)
    				    let end = moment(this.user.trip.date_end)
    				    let now = moment()

                        let count = end.diff(start, 'days') + 1;
                        let past = start.diff(now, 'days');

                        this.user.trip.days = {
                            count: count,
                            past: Math.abs(past),
                            percentage: ((Number(past)*100)/Number(count)).toFixed(0),
                            days: []
                        }

                        let ddate = end;

                        for(let i = 0;i < count; i++)
                        {
                            

                            if(i == 0)
                            {
                            }
                            else{
                                ddate = end.subtract(1,'days');
                            }

                            this.user.trip.days.days.push({
                                date: ddate.format('MM-DD-Y'),
                                name: ddate.format('dddd')
                            })
                            console.log(i, ddate.format('MM-DD-Y'))
                        }

      					$('.days-bar').css('width', this.user.trip.days.percentage + '%')

      					console.log(this.user.trip.days)

      				}
      			}
                $(".preloader").fadeOut();

  			})
  		}
  	}

  	addPayment()
  	{


  	}

  	ngOnInit() {
  	}

}



