export class Config{

	api: any;
	socket: any;

	constructor()
	{
		//let host = 'http://sivoz.dyndns.info/viaticos/';
		let host = 'http://localhost/viaticos/';

		this.api = {
			index: host,
			controller: host ,
			files: host + 'public/assets/',
			debug: false,
			logo: host + 'public/assets/images/logo.png'
		}
	}

	getApi()
	{
		return this.api;
	}

	getEnviroment()
	{
		return true;
	}
}


